#contents_wrap{
    width: 100%;
}
.menu{
    clear: both;
    box-sizing: border-box;
    width: 100%;
    height: 40px;
    margin-bottom: 20px;
}
.menu ul{
    width: 90%;
    height: 40px;
    margin:20px auto;
}
.menu ul li{
    width: 50%;
    height: 40px;
    line-height: 40px;
    float: left;
    text-align: center;
    font-size: 15px;
    border: 1px solid #333;
    box-sizing: border-box;
}
.menu ul li:hover a{
    background-color: #000;
    color:#fff;
    font-weight: bold;
}
.menu ul li:last-child{
    border-left: none;
}
.menu ul li a{
    color: #000;
    display: block;
}