#category_wrap{
    width: 100%;
}
.menu{
    width: 100%;
    height: 30px;
    margin-bottom: 30px;
}
.menu ul{
    width: 100%;
}
.menu ul li{
    width: 20%;
    float: left;
    text-align: center;
    border-left: 1px solid #666;
    box-sizing: border-box;
}
.menu ul li:first-child{
    border-left: none;
}
.menu ul li a{
    color:#666;
    font-size: 13px;
    display: block;
}
.menu ul li:hover a{
    font-weight: bold;
    color: #000;
}
.con{
    width: 100%;
    height: 280px;
    box-sizing: border-box;
}

.contents>div {
    float: left;
    width: 48%;
    height: 190px;
    border:1px solid rgba(0,0,0,0.3);
    margin-left: 1%;
    box-sizing: border-box;
}
.contents>div:hover{
    border:2px solid #000;
    font-weight: bold;
}
.contents>div>p:nth-child(2) {
    font-size: 16px;
    margin-left: 2%;    
}
.con_title{
    width: 100%;
    height: 20px;
    font-size: 20px;
    margin-bottom: 30px;
    position: relative;
    font-weight: bold;
}
.con_title p{
    position: absolute;
}
.con_title p:nth-child(1){
    left:50%;
    transform: translate(-50%,0);
}
.con_title p:nth-child(2){
    right: 10px;
    font-size: 13px;
}
.con_title > p:nth-child(2) a{
    color:#666;
}