#pick_wrap {
    clear: both;
    width: 100%;
    min-width: 320px;
}

.main_img {
    clear: both;
    width:100%;
    height:300px;
    margin-bottom: 70px;
}

/* 오늘의 1분 반려지식 */
.pick_con1{
    width: 100%;
    height: 480px;
    box-sizing: border-box;
}

.con1_con>div {
    float: left;
    width: 48%;
    height: 190px;
    border:1px solid rgba(0,0,0,0.3);
    margin-left: 1%;
    box-sizing: border-box;
    margin-bottom: 10px;
}
.con1_con>div:hover{
    border:2px solid #000;
    font-weight: bold;
}
.con1_con>div>p:nth-child(2) {
    font-size: 16px;
    margin-left: 2%;    
}
.con1_title{
    width: 100%;
    height: 20px;
    font-size: 20px;
    margin-bottom: 30px;
    position: relative;
    font-weight: bold;
}
.con1_title p{
    position: absolute;
}
.con1_title p:nth-child(1){
    left:50%;
    transform: translate(-50%,0);
}
.con1_title p:nth-child(2){
    right: 10px;
    font-size: 13px;
}
.con1_title > p:nth-child(2) a{
    color:#666;
}



