#main {
    box-sizing: border-box;
    width: 100%;
    min-width: 320px;
    height: 100%;
}
.main_img{
    width:100%;
    height:300px;
    margin-bottom: 70px;
}
.menu{
    width: 90%;
    height: 250px;
    margin:0 auto;
}
.menu li{
    width: 33.3%;
    height: 60px;
    line-height: 60px;
    float: left;
    text-align: center;
    background: url(../../public/images/main/menu_img.png) no-repeat center top;
    background-size: contain;
    transition: all 0.1s ease-in;
}
.menu li:hover{
    scale: 1.1;
    font-weight: bold;
    background: url(../../public/images/main/menu_img2.png) no-repeat center top;
    background-size: contain;
}
.menu li a{
    color:#000;
    font-size: 16px;
}

/* 댕댕이와 함께 즐기는... */
.con1{
    clear: both;
    width: 100%;
    height: 270px;
    margin:20px auto;
    box-sizing: border-box;
}

.con1_title{
    width: 100%;
    height: 20px;
    font-size: 18px;
    margin-bottom: 10px;
    position: relative;
    font-weight: bold;
    letter-spacing: -1px;
}
.con1_title p{
    position:absolute;
}
.con1_title p:nth-child(1){
    left:50%;
    transform: translate(-50%,0);
}
.con1_title p:nth-child(2){
    right:10px;
}
.con1_title > p:nth-child(2) a{
    color:#000;
    display: block;
}
.con1_con{
    float: left;
    width: 48%;
    height: 200px;
    border:1px solid rgba(0,0,0,0.3);
    margin-left: 1%;
    box-sizing: border-box;
}
.con1_con:hover{
    border:2px solid #000;
}
.con1_txt p{
    font-size: 15px;
    line-height: 1.2em;
    margin-left: 5%;
}
.con1_txt p:nth-child(1){
    margin-bottom: 15px;
    color:#333;
}
.con1_txt p:nth-child(2){
    color:#000;
    font-weight: bold;
    letter-spacing: 1px;
}
.con1_txt p:nth-child(2) span{
    float:right;
    margin-right: 2%;
    letter-spacing: -1px;
}


