html,
body {
  width: 100%;
  height: 100%;
}

input {
  outline: none;
}

body,
input {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  font: normal 15px/1.5em nanum, sans-serif;
  color: #000;
}

#top {
  width: 100%;
  height: 60px;
  position: relative;
  background-color: #000;
}

.all_menu {
  position: absolute;
  height: 60px;
  left:10px;
  top:10px;
}

.menu_wrap {
  width: 200px;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.9);
  position: fixed;
  left: -50%;
  top: 0;
  z-index: 10;
  overflow: hidden;
  transition: left 0.5s;
}

.menu {
  margin-top: 100px;
}

.menu li {
  text-indent: 2em;
  line-height: 2.1em;
}

.menu li a {
  color: #ffffff;
}

.menu_close {
  color: #ffffff;
  position: absolute;
  right: 30px;
  top: 40px;
  font-size: 30px;
  cursor: pointer;
  z-index: 11;
}

.menu .active {
  color: red;
  font-weight: bold;
}

.toggle {
  left: 0;
}

#top h1 {
  height: 60px;
  text-align: center;
  font-family: verdana, sans-serif;
  font-size: 24px;
  line-height: 60px;
  text-indent: -10%;
  position: absolute;
  left:52%;
  top:50%;
  transform: translate(-52%,-50%);
}

#top h1 a {
  color: #ffffff;
}

#main_menu {
  clear: both;
  width: 100%;
  height: 40px;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  left: 0;
}

#main_menu ul {
  width: 100%;
  height: 40px;
  background-color: #eaeaea;
}

#main_menu ul li {
  float: left;
  width: 33%;
  height: 40px;
  text-align: center;
  line-height: 40px;
}

#main_menu ul li a {
  color: #000;
  font-weight: bold;
  font-size: 16px;
}

#main_menu ul li .active {
  color: red;
  font-weight: bold;
}