#event_wrap{
    width: 100%;
}
.event_con{
    width: 100%;
    height: 400px;
    margin: 50px auto;
    cursor: pointer;
}
.event_con:hover .txt{
    font-weight: bold;
}
.event_title{
    text-align: center;
    font-size: 25px;
    font-weight: bold;
    margin-bottom: 20px;
}
.txt{
    font-size: 18px;
    text-align: center;
}