#bottom{
    clear: both;
    background-color: #000;
    color:#fff;
    width: 100%;
    height: 200px;
    position: relative;
}
#bottom > p{
    font-size: 18px;
    height: 30px;
    position:absolute;
    left:20%;
    top:10%;
    transform: translate(-20%,-10%);
}
.bottom_txt{
    position:absolute;
    top:50%;
    left:25%;
    transform: translate(-20%,-50%);
    font-size: 12px;
    color:#999;
    line-height: 1.2em;
}
#bottom > p span a{
    color:#fff;
}
#bottom > ul{
    width: 90%;
    position: absolute;
    bottom: 5%;
    left:50%;
    transform: translate(-50%,-5%);
    margin:0 auto;
}
#bottom > ul li{
    float: left;
    width: 30%;
    text-align: center;
    margin-left: 2.5%;
}
#bottom > ul li:first-child{
    margin-left: 0;
}
#bottom > ul li a{
    color:#999;
    font-size: 12px;
}